:root {
  --outer-space: #3e4e50;
  --apricot: #facfad;
  --fawn: #F8BD7F;
  --sandy-brown1: #F5AC72;
  --sandy-brown2: #f2aa7e;
  font-family: 'Roboto', sans-serif;
}

.App {
  text-align: center;
  display: flex;
}


/* Styles for wider than tall screens */
@media (min-aspect-ratio: 1/1) {
  .App {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between; /* Optional: Adds space between items */
    height: 100vh;
  }
}

/* Styles for taller than wide screens */
@media (max-aspect-ratio: 1/1) {
  .App {
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between; /* Optional: Adds space between items */
    width: 100vw;
    min-height: 100vh;
  }
}

.IngredientSearch{
  background-color: rgb(87, 91, 90);
  flex: 1;
  display: flex;
  flex-direction: column;
}

.FoodItem{
  position: 'relative';
  
  width: '100px';
  height: '100px';
  background-color: 'red';
  cursor: 'grab'
}

.IngredientSearchBox {
  height: 20px;
}

.IngredientResultPane {
  display: flex;
  flex-wrap: wrap;
  max-height: 200px;
  justify-content: space-between;
  flex: 1;
}

.IngredientResult {
  background-color: var(--sandy-brown1);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10%;
  padding: 1%;
  margin: 1%
}

.MealPlan{
  background-color: var(--apricot);
  flex: 1;
  overflow: scroll;
}

.MealPlanContainer {
  background-color: white;
  flex: 1;
}

.MealItem{
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: 5px;
  padding: 5px;
  background-color: var(--sandy-brown2);
  border-radius: 10px;
}

.MIControls{
  display: flex;
  flex-direction: row;
  align-content: center;
}

.MICName{
  flex-grow: 4;
}

.MICInput{
  align-self: center;
  max-width: 40px;
  max-height: 20px;
}

.MICXButton {
  align-self: center;
  height: 20px;
  width: 20px;
  align-self: center;

}

.MIMacros{

}

.MacroSummary{
  background-color: var(--sandy-brown1);
  flex: 1;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
